<template>
	<div style="text-align: center;" v-if="user">
		<img style="width: 100px;height: 100px;border-radius: 50%;border: 1px solid;" :src="user.headimgurl"/>
		<div>{{user.nickname}}--{{user.sex==1?'男':'女'}}</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				user: null
			}
		},
		created() {
			const userStr = sessionStorage.getItem('user');
			this.user = JSON.parse(userStr);
		}
	}
</script>

